import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
// Style.
import './index.scss';
// Pure Main comp & context.
import App from './App';
  // Context.
  import { Component_context_lang_theme } from '~/Context_store/Theme_language/Theme_language'
// React router.
import { HashRouter } from "react-router-dom";
// Inter language.
import { IntlProvider } from 'react-intl'
import { messages } from '~/Context_store/Theme_language/LangTextMessage'
import { LOCALES } from '~UX_COMP/Inter_lang/locales'
//
const App_Index = () => {
  // Index state theme & lang.
  const [currentLocale, setCurrentLocale] = useState(LOCALES.POLISH)
  // Change context state.
  const handleChange = ({ target: { value } }) => {
      setCurrentLocale(value)
  }
  // -
  return (
    // --
    <IntlProvider
      messages={messages[currentLocale]}
      locale={currentLocale}
      defaultLocale={LOCALES.POLISH}
    >
      {/* // */}
      <HashRouter>
        <Component_context_lang_theme
          currentLocale={currentLocale}
          handleChange={handleChange}
        >
          <App />
        </Component_context_lang_theme>
      </HashRouter>
      {/* // */}
    </IntlProvider>
    // --
  )
  // -
}
//
const root = ReactDOM.createRoot(document.getElementById('app'));
root.render(
  <React.StrictMode>
    <App_Index />
  </React.StrictMode>
);

