import React from 'react'
//
import { Routes, Route, Link } from 'react-router-dom';
//
const Main_page_layout = React.lazy(() => import('~UI_LAYOUT/Main_page_layout/Main_page_layout'));
const Partner = React.lazy(() => import('~UI_LAYOUT/Partner/Partner'));
const Faq = React.lazy(() => import('~UI_LAYOUT/Faq/Faq'));

const Exchange = React.lazy(() => import('~UX_COMP/Exchange/Exchange'));
// const Panel = React.lazy(() => import('./Pages/Panel/Panel'));

//
const Navigation = ({

}) => {
//
  return (
    <Routes>

        <Route
          index
          element={
            <React.Suspense fallback={<>...</>}>
              <Main_page_layout child_Route_component={ <Exchange /> } />
            </React.Suspense>
          }
        />

        <Route
          path="crypto-check"
          element={
            <React.Suspense fallback={<>...</>}>
              <Main_page_layout child_Route_component={ 'crypto-check' } />
            </React.Suspense>
          }
        />


        <Route
            path="partner"
            element={
              <React.Suspense fallback={<>...</>}>
                <Main_page_layout child_Route_component={ <Partner /> } />
              </React.Suspense>
            }
        />

        <Route
            path="faq"
            element={
              <React.Suspense fallback={<>...</>}>
                <Main_page_layout child_Route_component={ <Faq /> } />
              </React.Suspense>
            }
        />

        <Route path="*" element={<> Not known path </>} />

    </Routes>
  )

}

export default Navigation